import React from 'react';
import iconPassangerSircle from '../../assets/images/icon_passanger_sircle.png';
import styles from './styles.module.scss';

const Passenger = (props) => {
  const isAdult = props.personInfo.is_adult === 'true' ? '' : '';
  const gender = !props.personInfo.gender ? '' : '';
  const documentType = props.personInfo.document_type === '' ? ' ' : '  ';

  return (
    <div className={styles.passenger}>
      <div className={styles.passenger__profile}>
        <img src={iconPassangerSircle} alt="..." />
        <h4>{isAdult}</h4>
      </div>
      <div className={styles.passenger__data}>
        <h4>
          {props.personInfo.last_name} {props.personInfo.first_name} {props.personInfo.patronymic}
        </h4>
        <p> {gender}</p>
        <p> : {props.personInfo.birthday}</p>
        <p>
          {documentType}: {props.personInfo.document_data}
        </p>
      </div>
    </div>
  );
};

export default Passenger;
